@mixin padtophone{
  @media (max-width:767px) {
      @content
  }
}

@mixin padtodesk{
  @media (max-width:992px) {
      @content
  }
}

.index__h1 {
  color:#00cc99;
  font-size: 20px;
  font-weight: 400;
}

h2 {
  font-family: 'Montserrat', sans-serif;
}

.headpage {
  height: 100vh;
}

.container-xxl {
  max-width: 100vw;
  position: fixed;
}

.menu-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all .3s ease-in-out;
  z-index: 3;
}

.menu-burger {
  width: 30px;
  height: 3px;
  background-color: #000;
  border-radius: 5px;
  transition: all .3s ease-in-out;
  position: absolute;
}

.menu-burger::before,
.menu-burger::after {
  content: '';
  width: 30px;
  height: 3px;
  background-color: #000;
  border-radius: 5px;
  transition: all .3s ease-in-out;
  position: absolute;
}

.menu-burger::before {
  transform: translateY(-10px);
}

.menu-burger::after {
  transform: translateY(10px);
}

// animation
.menu-btn.open .menu-burger {
  transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.dropdown {
  @include padtodesk(){
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 100vh;
    overflow: hidden;
    transform: translateX(100vw);
    background-color: #fff;
    z-index: 2;
    transition: transform 0.3s ease-in-out;
  }
  &.open {
    @include padtodesk(){
      transform: translateX(0);
    }
  }
  & li a {
    @include padtodesk(){
      font-size: 4vh;
    }
  }
}

.menu {
  li {
    a {
      @include padtodesk(){
        font-size: 48px!important;
      }
      @include padtophone(){
        font-size: 32px!important;
      }
    }
  }
}

.scroll {
  position: relative;
  z-index: -1;
}